* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  text-decoration: none;
  outline: none;
  list-style: none;
  font-family: sans-serif;
  border: none;
}

:root {
  --webColor: rgb(253,86,49);
  --darkBlack: rgb(31,27,45);
  --lightBlack: rgb(102,98,118);
  --white: #fff;
  --green: rgb(7,201,139);
  --red: rgb(242,60,73);
  --yellow: rgb(253,188,49);
  --webColorOpacity: rgba(253,86,49, 0.2);
  --boxShadowWeb: 3px 6px 5px 1px rgba(253,86,49,0.7);
  --boxShodowBlack: 0 0 15px 1px rgba(0,0,0,0.2);
}

h1,
h2 {
  color: var(--darkBlack);
}

a,
button {
  cursor: pointer;
}

html {
  scroll-behavior: smooth;
}

.section {
  width: 100%;
  padding: 50px 20px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.container {
  max-width: 1250px;
  width: 100%;
  display: flex;
}

.title {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.title h2 {
  font-size: 2.2em;
  color: var(--darkBlack);
}

.title a {
  font-size: 1.1em;
  color: var(--lightBlack);
  transition: all 0.5s;
}

.title a i {
  font-size: 0.9em;
  transition: all 0.5s;
}

.title a:hover {
  color: var(--webColor);
}

.title a:hover i {
  transform: translateX(5px);
  color: var(--webColor);
}

.section .container .slider {
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  width: 100%;
}

.section .container .slider .cont {
  overflow: hidden;
  padding: 20px 0px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.section .container .slider .lRBtn {
  position: absolute;
  display: none;
  top: 50%;
  transform: translateY(-50%);
  font-size: 1em;
  color: var(--darkBlack);
  box-shadow: 0 0 15px 2px rgba(0, 0, 0, 0.2);
  z-index: 10;
  width: 40px;
  height: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  cursor: pointer;
  text-shadow: 0 0 5px 3px rgba(0, 0, 0, 0.2);
  transition: all 0.3s;
}

.section .container .slider .lRBtn:hover {
  color: var(--webColor);
}

.section .container .slider .lRBtn[disabled] {
  color: var(--webColor);
}

.section .container .slider .left {
  left: 0px;
}

.section .container .slider .right {
  right: 0px;
}

.section .container .slider .circle {
  position: absolute;
  bottom: -50px;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 10px;
  left: 50%;
  transform: translateX(-50%);
  z-index: 100;
}

.section .container .slider .circle .circleBtn {
  width: 16px;
  height: 8px;
  background: rgba(0, 0, 0, 0.3);
  border-radius: 30px;
  cursor: pointer;
}

.section .container .slider .circle .circleBtn.bold {
  width: 32px;
  background: rgba(0, 0, 0, 0.6);
  transition: all 0.5s;
}

nav {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1000000;
  width: 100%;
  padding: 20px 20px;
  background: transparent;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: all 0.2s;
}

nav.active {
  background: var(--white);
  filter: drop-shadow(0px 6px 3px rgba(0, 0, 0, 0.1));
}

nav .responsiveMenu {
  display: none;
  background: var(--white);
}

nav .nav-container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 20px;
}

nav .nav-container .logo {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 5px;
}

nav .nav-container .logo i {
  font-size: 1.7em;
  color: var(--webColor);
}

nav .nav-container .logo span {
  color: var(--darkBlack);
  font-size: 1.3em;
  font-weight: normal;
}

nav .nav-container .links {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 55px;
}

nav .nav-container .links .link {
  font-size: 16px;
  color: var(--lightBlack);
  cursor: pointer;
  font-weight: bold;
  transition: all 0.5s;
}

nav .nav-container .links .link:hover {
  color: var(--webColor);
}

nav .nav-container .links .big {
  position: relative;
}

nav .nav-container .links .big .bigLink {
  display: flex;
  align-items: center;
  gap: 3px;
}

nav .nav-container .links .big .bigLink i {
  font-size: 12px;
}

nav .nav-container .links .big ul {
  position: absolute;
  background: var(--white);
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  gap: 10px;
  width: 150px;
  bottom: -95px;
  opacity: 0;
  left: -10px;
  padding: 15px;
  border-radius: 5px;
  box-shadow: var(--boxShodowBlack);
  visibility: hidden;
  transition: all 0.5s;
}

nav .nav-container .links .big ul.active {
  visibility: visible;
  opacity: 1;
  bottom: -90px;
}

nav .nav-container .links .big ul:hover {
  visibility: visible;
  opacity: 1;
  bottom: -90px;
}

nav .nav-container .links .big ul li a {
  color: var(--lightBlack);
  font-size: 15px;
  transition: all 0.5s;
}

nav .nav-container .links .big ul li a:hover {
  color: var(--webColor);
}

nav .nav-container .icons {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 15px;
}

nav .nav-container .icons .onlyIcon {
  width: 45px;
  font-size: 1.1em;
}

nav .nav-container .icons .icon {
  padding: 12px;
  height: 45px;
  display: flex;
  justify-content: center;
  align-items: center;
  background: var(--webColor);
  color: var(--white);
  gap: 5px;
  border-radius: 5px;
  transition: 0.5s;
}

nav .nav-container .icons .icon:hover {
  box-shadow: var(--boxShadowWeb);
}

nav .nav-container .icons .icon.respBtn {
  display: none;
}

nav .nav-container .icons .strB span {
  font-size: 16px;
}

