#home {
  min-height: 100vh;
  padding-top: 85px;
}

#home .homeContainer .homeContent {
  max-width: 550px;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  gap: 50px;
  z-index: 10;
}

#home .homeContainer .homeContent h1 {
  font-size: 3em;
}

#home .homeContainer .homeContent p {
  color: var(--lightBlack);
  font-size: 1.1em;
  line-height: 28px;
}

#home .homeContainer .homeImage {
  position: relative;
  max-width: 750px;
  width: 100%;
  left: -50px;
  top: 0;
}

#home .homeContainer .homeImage img {
  width: calc(100% + 50px);
  background-size: cover;
}

.prototype .cards {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 50px;
}

.prototype .cards .card {
  border-radius: 5px;
  box-shadow: 0 0 15px 2px rgba(0, 0, 0, 0.1);
  padding: 35px 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 20px;
  flex-direction: column;
  transition: all 0.5s;
}

.prototype .cards .card .icon {
  width: 50px;
  height: 50px;
  background: var(--webColorOpacity);
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  cursor: pointer;
}

.prototype .cards .card .icon i {
  color: var(--webColor);
  font-size: 1.3em;
  transition: all 0.5s;
}

.prototype .cards .card:hover {
  box-shadow: 0 0 15px 1px rgba(0, 0, 0, 0.2);
}

.prototype .cards .card:hover .icon {
  background: var(--webColor);
}

.prototype .cards .card:hover .icon i {
  color: var(--white);
}

.prototype .cards .card h3 {
  font-size: 1.3em;
  color: var(--darkBlack);
}

.webLinkContainer {
  justify-content: space-between;
  gap: 20px;
  flex-wrap: nowrap;
}

.webLinkContainer .card {
  width: 370px;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 25px;
  flex-direction: column;
  gap: 30px;
  transition: all 0.5s;
}

.webLinkContainer .card img {
  width: 100%;
  padding: 0px 20px;
  background-size: cover;
}

.webLinkContainer .card .content {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 15px;
  text-align: center;
}

.webLinkContainer .card .content h3 {
  font-size: 1.3em;
  color: var(--darkBlack);
}

.webLinkContainer .card .content p {
  color: var(--lightBlack);
  line-height: 25px;
}

.webLinkContainer .card .content .button {
  padding: 10px 20px;
  border-radius: 5px;
  border: 2px solid var(--webColor);
  color: var(--webColor);
  font-size: 1.1em;
  cursor: pointer;
  transition: all 0.5s;
}

.webLinkContainer .card .content .button:hover {
  background: var(--webColor);
  color: var(--white);
}

.webLinkContainer .card:hover {
  box-shadow: var(--boxShodowBlack);
}

.offerContainer {
  flex-direction: column;
  gap: 30px;
  position: relative;
}

.offerContainer .slider .cont {
  max-width: 1150px;
  width: 100%;
}

.offerContainer .slider .cont .cards {
  width: 100%;
  display: flex;
  justify-content: flex-start;
  flex-wrap: nowrap;
  gap: 15px;
  position: relative;
  padding: 22px 10px;
  transition: all 0.5s;
}

.offerContainer .slider .cont .cards .card {
  display: flex;
  flex-direction: column;
  min-width: 272px;
  width: 100%;
  border-radius: 15px;
  gap: 20px;
  box-shadow: 0 0 5px 1px rgba(0, 0, 0, 0.1);
  transition: all 0.5s;
}

.offerContainer .slider .cont .cards .card .image {
  width: 100%;
  height: 200px;
  position: relative;
}

.offerContainer .slider .cont .cards .card .image img {
  width: 100%;
  height: 100%;
  border-top-left-radius: 15px;
  border-top-right-radius: 15px;
  background-size: cover;
  cursor: pointer;
}

.offerContainer .slider .cont .cards .card .image .cardHover {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  border-top-left-radius: 15px;
  border-top-right-radius: 15px;
  z-index: 3;
  cursor: pointer;
  opacity: 0;
  pointer-events: none;
  background: rgba(31, 27, 45, 0.4);
  transition: all 0.5s;
}

.offerContainer .slider .cont .cards .card .image .cardHover .likeIcon {
  position: absolute;
  right: 15px;
  top: 15px;
  width: 35px;
  height: 35px;
  background: var(--white);
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: all 0.5s;
}

.offerContainer .slider .cont .cards .card .image .cardHover .likeIcon i {
  color: var(--webColor);
}

.offerContainer .slider .cont .cards .card .image .cardHover .likeIcon:hover {
  background: var(--webColor);
}

.offerContainer .slider .cont .cards .card .image .cardHover .likeIcon:hover i {
  color: var(--white);
}

.offerContainer .slider .cont .cards .card .content {
  display: flex;
  flex-direction: column;
  gap: 13px;
  padding: 0 15px;
}

.offerContainer .slider .cont .cards .card .content:hover .info {
  color: var(--webColor);
}

.offerContainer .slider .cont .cards .card .content .status {
  font-size: 0.8em;
  text-transform: uppercase;
  color: var(--webColor);
}

.offerContainer .slider .cont .cards .card .content .info {
  font-size: 1.1em;
  color: var(--darkBlack);
  transition: all 0.5s;
}

.offerContainer .slider .cont .cards .card .content .address {
  font-size: 0.9em;
  color: rgba(0, 0, 0, 0.5);
}

.offerContainer .slider .cont .cards .card .content .price {
  display: flex;
  align-items: center;
  gap: 5px;
  color: rgba(0, 0, 0, 0.6);
  font-weight: bold;
}

.offerContainer .slider .cont .cards .card .cardFooter {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 8px 0px;
  margin: 0 auto;
  width: 90%;
  border-top: 1px solid rgba(0, 0, 0, 0.2);
  gap: 25px;
}

.offerContainer .slider .cont .cards .card .cardFooter .info {
  display: flex;
  align-items: center;
  color: var(--lightBlack);
  gap: 5px;
}

.offerContainer .slider .cont .cards .card:hover .image .cardHover {
  pointer-events: painted;
  opacity: 1;
}

.newAddedContainer {
  flex-direction: column;
  gap: 40px;
}

.newAddedContainer .title {
  width: 100%;
  justify-content: space-between;
}

.newAddedContainer .cards {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: 300px 300px;
  gap: 20px;
}

.newAddedContainer .cards .item {
  grid-row: 1/span 2;
}

.newAddedContainer .cards .card {
  width: 100%;
  border-radius: 15px;
  position: relative;
}

.newAddedContainer .cards .card img {
  width: 100%;
  height: 100%;
  border-radius: 15px;
  object-fit: cover;
}

.newAddedContainer .cards .card .position {
  position: absolute;
  top: 0;
  left: 0;
  border-radius: 15px;
  box-shadow: 0 -100px 50px 35px rgba(31, 27, 45, 0.7) inset;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: flex-end;
  padding: 20px;
}

.newAddedContainer .cards .card .position .content {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  flex-wrap: wrap;
  gap: 20px;
}

.newAddedContainer .cards .card .position .content .left {
  display: flex;
  flex-direction: column;
  gap: 15px;
}

.newAddedContainer .cards .card .position .content .left .property {
  font-size: 0.9em;
  color: var(--white);
}

.newAddedContainer .cards .card .position .content .left h3 {
  font-size: 1.5em;
  color: var(--white);
}

.newAddedContainer .cards .card .position .content .left p {
  color: rgba(255, 255, 255, 0.7);
  font-size: 1em;
  display: flex;
  gap: 5px;
}

.newAddedContainer .cards .card .position .content .right {
  display: flex;
}

.newAddedContainer .cards .card .position .content .right .button {
  padding: 15px;
  background: var(--webColor);
  color: var(--white);
  transition: all 0.5s;
}

.newAddedContainer .cards .card .position .content .right .button:last-child {
  border-left: 1px solid rgba(255, 255, 255, 0.6);
  border-top-right-radius: 10px;
  border-bottom-right-radius: 10px;
}

.newAddedContainer .cards .card .position .content .right .button:first-child {
  border-top-left-radius: 10px;
  border-bottom-left-radius: 10px;
}

.newAddedContainer .cards .card .position .content .right .button:hover {
  background: rgb(253, 58, 13);
  box-shadow: 0 5px 5px 2px rgba(253, 58, 13, 0.3);
}

.citiesContainer {
  flex-direction: column;
  gap: 40px;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.citiesContainer .title {
  width: 100%;
}

.citiesContainer .cards {
  width: 100%;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 15px;
  transition: all 0.5s;
}

.citiesContainer .cards .card {
  display: flex;
  flex-direction: column;
  border-radius: 15px;
  box-shadow: 0 0 5px 1px rgba(0, 0, 0, 0.1);
  transition: all 0.5s;
}

.citiesContainer .cards .card:hover {
  box-shadow: 0 0 10px 2px rgba(0, 0, 0, 0.2);
}

.citiesContainer .cards .card .img img {
  width: 100%;
  background-size: cover;
  border-radius: 15px;
}

.citiesContainer .cards .card h3 {
  text-align: center;
  padding: 20px;
  font-size: 1.2em;
  color: var(--darkBlack);
}

@media screen and (max-width: 1300px) {
  .section .container .title {
    width: 100%;
  }

  .section .container .slider .cont {
    max-width: 1000px;
    width: 100%;
  }

  .section .container .slider .cont .cards {
    max-width: 1000px;
    width: 100%;
    justify-content: flex-start;
  }

  .section .container .slider .cont .cards .card {
    min-width: calc(33.3333333333% - 10px);
  }
}

@media screen and (max-width: 1150px) {
  .section .container .slider .cont {
    max-width: 800px;
  }
}

@media screen and (max-width: 1000px) {
  .container {
    max-width: 750px;
  }

  nav .nav-container .links {
    gap: 20px;
  }

  .container {
    max-width: 750px;
  }

  #home .homeContainer {
    flex-direction: column;
    align-items: center;
    gap: 50px;
  }

  #home .homeContainer .homeContent {
    max-width: 750px;
    width: 100%;
    order: 2;
    gap: 20px;
  }

  #home .homeContainer .homeContent h1 {
    max-width: 500px;
    text-align: center;
  }

  #home .homeContainer .homeContent p {
    font-size: 1.3em;
    text-align: center;
  }

  #home .homeContainer .homeImage {
    width: 100%;
    top: 0;
    left: 0;
    order: 0;
  }

  #home .homeContainer .homeImage img {
    width: 100%;
  }

  .prototype .cards {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
  }

  .webLinkContainer {
    justify-content: center;
    gap: 20px;
    flex-wrap: wrap;
  }

  .webLinkContainer .card {
    width: 365px;
  }

  .section .container .slider .cont {
    max-width: 600px;
  }

  .section .container .slider .cont .cards .card {
    min-width: calc(50% - 5px);
  }

  .newAddedContainer .cards {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
    gap: 20px;
  }

  .newAddedContainer .cards .card {
    width: 100%;
    max-height: 500px;
    overflow: hidden;
    position: relative;
  }

  .newAddedContainer .cards .card img {
    width: 100%;
    height: 100%;
    background-size: cover;
  }

  .citiesContainer .cards {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media screen and (max-width: 810px) {
  .webLinkContainer .card {
    width: 300px;
  }
}

@media screen and (max-width: 750px) {
  .section .container .slider .cont {
    max-width: 420px;
  }

  .section .container .slider .cont .cards .card {
    min-width: 400px;
  }
}

@media screen and (max-width: 720px) {
  nav .nav-container .links {
    display: none;
  }

  nav .nav-container .icons .icon.respBtn {
    display: flex;
  }

  nav .responsiveMenu {
    display: flex;
    position: fixed;
    top: 85px;
    left: 0;
    padding: 20px;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    clip-path: polygon(0 0, 100% 0, 100% 0, 0 0);
    transition: all 0.5s;
  }

  nav .responsiveMenu .links {
    display: flex;
    flex-direction: column;
    gap: 15px;
    width: 100%;
  }

  nav .responsiveMenu .links .link {
    width: 100%;
    border-radius: 5px;
    border: 1px solid var(--lightBlack);
    padding: 12px;
  }

  nav .responsiveMenu .links .link span {
    display: flex;
    justify-content: space-between;
    transition: all 0.5s;
    color: var(--darkBlack);
    font-size: 1.1em;
  }

  nav .responsiveMenu .links .link:hover span {
    color: var(--webColor);
  }

  nav .responsiveMenu .links .big {
    display: flex;
    flex-direction: column;
    gap: 10px;
  }

  nav .responsiveMenu .links .big .bacvi {
    display: none;
    flex-direction: column;
    gap: 5px;
    height: 0;
    overflow: hidden;
    border-left: 3px solid rgba(0, 0, 0, 0.3);
    transition: all 0.5s;
  }

  nav .responsiveMenu .links .big .bacvi a {
    margin-left: 15px;
    font-size: 0.9em;
    color: var(--darkBlack);
  }

  nav .responsiveMenu .links .big .bacvi a:hover {
    color: var(--webColor);
  }

  nav .responsiveMenu .links .big.active span {
    color: var(--webColor);
  }

  nav .responsiveMenu .links .big.active .bacvi {
    display: flex;
    height: 100%;
  }

  nav .responsiveMenu.active {
    clip-path: polygon(0 0, 100% 0, 100% 100%, 0 100%);
  }
}

@media screen and (max-width: 680px) {
  .webLinkContainer .card {
    width: 250px;
  }
}

@media screen and (max-width: 580px) {
  .webLinkContainer .card {
    max-width: 450px;
    width: 100%;
  }

  .newAddedContainer .cards .card .position .content {
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
  }
}

@media screen and (max-width: 550px) {
  .section .container .slider .cont {
    max-width: 270px;
  }

  .section .container .slider .cont .cards .card {
    min-width: 250px;
  }

  .citiesContainer .title h2 {
    font-size: 1.3em;
  }

  .citiesContainer .cards {
    grid-template-columns: repeat(1, 10fr);
  }

  .citiesContainer .cards .card {
    width: 100%;
    align-self: center;
    justify-self: center;
  }
}

@media screen and (max-width: 500px) {
  nav .nav-container .icons button {
    width: 45px;
    font-size: 1.3em;
  }

  nav .nav-container .icons button span {
    display: none;
  }

  #home .homeContainer .homeContent h1 {
    width: 100%;
    font-size: 1.7em;
  }

  #home .homeContainer .homeContent p {
    font-size: 1em;
    line-height: 25px;
  }

  .prototype .cards {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 10px;
  }

  .prototype .cards .card h3 {
    font-size: 2em;
  }
}

@media screen and (max-width: 400px) {
  .prototype .cards {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    gap: 10px;
  }

  .section .container .slider .left {
    left: -15px;
  }

  .section .container .slider .right {
    right: -15px;
  }
}

@media screen and (max-width: 375px) {
  nav .nav-container .icons .icon {
    width: 40px;
    height: 40px;
    padding: 5px;
    font-size: 1.1em;
  }
}